/* -- Tipografía -- */
/* -- Colores -- */
/* -- Tamaños para los Media Queries -- */
/* -- TAMAÑO TELÉFONO -- */
/* -- TAMAÑO TABLET -- */
/* -- TAMAÑO DESKTOP -- */
/* -- TAMAÑO DESKTOP XL -- */
/* -- CSS GRID -- */
/* -- RESETEAR LISTA -- */
/* FORMATO AL DOCUMENTO HTML */
html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

/* ESTILOS PARA BODY */
body {
  font-family: "Monserrat", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 8.6rem;
}

/* PÁRRAFOS DE TODO EL SITIO */
p {
  font-size: 2rem;
  color: #000;
  line-height: 1.5;
}

/* ESTILO CONTENEDOR */
.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

/* ESTILOS PARA <a> DE TODO EL DOCUMENTO */
a {
  text-decoration: none;
}

/* ESTILOS PARA LOS HEADINS */
h1,
h2,
h3 {
  margin: 0 0 5rem 0;
}

/* <h1> */
h1 {
  font-size: 4rem;
}

/* <h2> */
h2 {
  font-size: 3.6rem;
}

/* <h3> */
h3 {
  font-size: 3rem;
}

/* ESTILOS PARA <img> DE TODO EL DOCUMENTO */
img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.header {
  background-color: #4cb8b3;
  width: 100%;
}
.header .titulo-principal,
.header .navegacion-principal {
  text-shadow: black 0.1em 0.1em 0.2em;
}
@media (min-width: 797px) {
  .header.fijo {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    box-shadow: 0.1rem 0.1rem 0.3rem #333;
    z-index: 10;
  }
}
.header h1 {
  color: #fff;
  text-align: center;
}
@media (min-width: 797px) {
  .header h1 {
    margin: 0;
  }
}
.header .contenido-header {
  padding: 2rem;
}
@media (min-width: 797px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

@media (min-width: 797px) {
  .navegacion-principal {
    display: flex;
    gap: 2rem;
  }
}
.navegacion-principal a {
  display: block;
  text-align: center;
  margin-bottom: 1rem;
  color: #fff;
  font-size: 2.4rem;
}
@media (min-width: 797px) {
  .navegacion-principal a {
    margin: 0;
  }
}
.navegacion-principal a:hover, .navegacion-principal a:focus {
  color: #fef46d;
}

body.header-fijo {
  padding-top: 6rem;
}

.site-footer {
  background-color: #f53756;
  padding: 1rem;
}
.site-footer p {
  margin: 0;
  text-align: center;
  color: #fff;
}

/* -- Estilos para.sobre-festival -- */
.sobre-festival {
  padding: 5rem 0;
  /* -- .sobre-festival en tamaño tablet -- */
}
@media (min-width: 797px) {
  .sobre-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

/* -- Estilos para .contenido-festival -- */
.contenido-festival {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5rem;
  /* -- .contenido-festival en tablet -- */
  /* -- Estilos para <h2> de .contenido-festival-- */
  /* -- Estilos para .fecha detro de .contenido-festival-- */
}
@media (min-width: 797px) {
  .contenido-festival {
    margin-top: 0;
  }
}
.contenido-festival h2 {
  margin-bottom: 2rem;
  font-size: 4rem;
}
.contenido-festival .fecha {
  color: #f53756;
  font-weight: 700;
}

.btn-volver-arriba {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  z-index: 999;
  display: none; /* Inicialmente oculto  */
  width: 3.8rem;
  height: 3.8rem;
  background-color: #4296f0;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.3s ease-in-out; /* Efectos de transición suaves */
  -webkit-box-shadow: 0 0 29.5px -6px #000000;
  -moz-box-shadow: 0 0 29.5px -6px #000000;
  box-shadow: 0 0 29.5px -6px #000000;
}
.btn-volver-arriba:hover {
  background-color: #1c81ed;
  -webkit-box-shadow: 0 0 29.5px 0.5px #000000;
  -moz-box-shadow: 0 0 29.5px 0.5px #000000;
  box-shadow: 0 0 29.5px 0.5px #000000;
}
.btn-volver-arriba:hover img {
  filter: invert(100);
}
.btn-volver-arriba img {
  max-width: 100%;
}

.btn-volver-arriba.visible {
  display: block; /* Mostrar botón al desplazarse */
}

.btn-volver-arriba:hover {
  /* Agregar efecto de desplazamiento */
  background-color: #4296f0;
}

/* ESTILOS PARA .boletos */
.boletos {
  padding: 5rem 0;
  /* h3 DE .boletos */
  /* .contenedor-boletos */
}
.boletos h3 {
  text-align: center;
  color: #fff;
  margin-bottom: 2rem;
  text-shadow: black 0.1em 0.1em 0.2em;
}
.boletos .contenedor-boletos {
  /* .boletos en tamaño tablet */
}
@media (min-width: 797px) {
  .boletos .contenedor-boletos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

/* ESTILOS PARA .pase */
.pase {
  /* .nombre- pase */
  /* <header> y <footer> de .pase */
}
.pase .nombre-pase {
  text-align: center;
  color: #fff;
  text-shadow: black 0.1em 0.1em 0.2em;
  font-weight: 400;
  font-size: 2.8rem;
}
.pase-header, .pase-footer {
  -webkit-box-shadow: 3px 2px 11px -1px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 3px 2px 11px -1px rgba(0, 0, 0, 0.75);
  box-shadow: 3px 2px 11px -1px rgba(0, 0, 0, 0.75);
  padding: 0.5rem;
}
.pase-header p, .pase-footer p {
  text-align: center;
  color: #fff;
  text-shadow: black 0.1em 0.1em 0.2em;
  margin: 0;
}
.pase-header {
  background-color: #f53756;
}
.pase-header p {
  text-transform: uppercase;
  font-size: 1.6rem;
}
.pase-body {
  -webkit-box-shadow: 3px 2px 11px -1px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 3px 2px 11px -1px rgba(0, 0, 0, 0.75);
  box-shadow: 3px 2px 11px -1px rgba(0, 0, 0, 0.75);
  background-color: #fff;
}
.pase-body ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.pase-body ul li {
  background-color: #e6e6e6;
  padding: 1rem;
  border-bottom: 1px solid #e1e1e1;
}
.pase-body ul li:nth-child(odd) {
  background-color: #fff;
}
.pase-footer {
  background-color: #752f77;
}
.pase-footer p {
  font-size: 2.4rem;
  font-weight: 700;
}

/* -- Estilos para galería -- */
.galeria {
  padding: 5rem 0;
  /* -- h3 de galería -- */
}
.galeria h3 {
  text-align: center;
}

/* -- imágenes de galería -- */
.galeria-imagenes {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
  /* -- Tamaño teléfono -- */
  /* -- Tamaño tablet -- */
  /* -- Tamaño desktop -- */
}
@media (min-width: 480px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 797px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

/* --  Estilos para el olverlay -- */
.overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay picture {
  display: block;
  max-width: 80rem;
}

.btn-cerrar {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-color: #fff;
  color: #000;
  display: grid;
  place-items: center;
  font-weight: 700;
}

.btn-cerrar:hover {
  cursor: pointer;
  background-color: #f53756;
  color: #fff;
}

/* -- Estilos para lineup -- */
.lineup {
  background-color: #f53756;
  padding: 5rem 0;
}
.lineup h3,
.lineup p {
  text-align: center;
  color: #fff;
  text-shadow: black 0.1em 0.1em 0.2em;
}
.lineup .dia {
  -webkit-box-shadow: 3px 2px 11px -1px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 3px 2px 11px -1px rgba(0, 0, 0, 0.75);
  box-shadow: 3px 2px 11px -1px rgba(0, 0, 0, 0.75);
  text-shadow: black 0.1em 0.1em 0.2em;
  text-transform: uppercase;
  margin: 5rem 30%;
  background-color: #d50b2c;
}

/* -- Estilos para para los escenarios -- */
@media (min-width: 797px) {
  .escenarios-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.escenarios-contenedor p {
  color: #000;
}

.escenario {
  -webkit-box-shadow: 3px 2px 11px -1px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 3px 2px 11px -1px rgba(0, 0, 0, 0.75);
  box-shadow: 3px 2px 11px -1px rgba(0, 0, 0, 0.75);
  padding-top: 2rem;
}
.escenario p:after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto 0 auto;
}

.rock .nombre-escenario:after {
  background-image: url("../img/icono_rock.png");
}

.edm .nombre-escenario:after {
  background-image: url("../img/icono_edm.png");
}

/* -- Estilos para el calendario -- */
.calendario {
  margin: 0;
  padding: 0;
  list-style: none;
}
.calendario li {
  padding: 2.5rem;
  font-weight: 700;
}
.calendario li span {
  font-weight: 400;
}

.bg-amarillo {
  background-color: #fef46d;
}
.bg-amarillo li:nth-child(odd) {
  background-color: #fdee21;
}

.bg-verde {
  background-color: #4cb8b3;
}
.bg-verde li:nth-child(odd) {
  background-color: #3b9691;
}

/* -- Estilos para .video -- */
.video {
  position: relative;
  min-height: 50rem;
  max-height: 70rem;
  overflow: hidden;
  /* -- Estilos para .overlay -- */
  /* -- Estilos para <video> -- */
}
.video .overlay {
  position: absolute;
  background: rgb(165, 3, 255);
  background: linear-gradient(270deg, rgba(165, 3, 255, 0.5) 0%, rgba(255, 0, 110, 0.3) 100%);
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
  /* -- <video> en tamaño desktop -- */
}
@media (min-width: 992px) {
  .video video {
    width: 100%;
  }
}

/* -- Estilos para .contenido-video -- */
.contenido-video {
  text-shadow: black 0.1em 0.1em 0.2em;
  z-index: 10;
  /* -- Estilos para <h2> de .contenido-video -- */
  /* -- Estilos para <p> de .contenido-video -- */
}
.contenido-video h2 {
  color: #fff;
  font-size: 7rem;
  margin: 0;
}
.contenido-video p {
  background-color: #f53756;
  display: inline-block;
  padding: 1rem 2rem;
  color: #fff;
  font-weight: 700;
  font-size: 2.4rem;
  margin: 0;
}